import React, { Component } from 'react';
import logo2 from "./images/logo2_03.jpg"
import put from "./images/put3_11.jpg"
import "./Login.css"

class Login extends Component {
  state={
    username:'',
    password:''
  }
changeUsername=(e)=>{
  this.setState({
    username:e.target.value
  })
}
changePassword=(e)=>{
  this.setState({
    password:e.target.value
  })
}
handleChlick=() => {

  this.props.login(this.state.username)
  this.props.hidentanchuang()
}
  render() {
  const {hidetanchuang} = this.props
  const {username,password} = this.state
    return (
      <div className="tanchuang">
        <div className="denglul">
          <a  onClick={hidetanchuang} href="javascript:;"></a>
          <img src={logo2} alt="" />
          <input type="text"
            value={username}
            onChange={this.changeUsername} placeholder="" />
          <span></span>
          <input type="password"
            value={password}
            onChange={this.changePassword}
          placeholder="" />
          <span>!</span>
          <input type="text" placeholder="" />
          <span></span>
          <img src={put} alt="" />
          <a
            onClick={this.handleChlick}
          href="javascript:;"></a>
          <a href="#"></a>
          <a href="#"></a>
          <span></span>
          <span></span>
        </div>
        <a className="fenxiang">
        </a>
      </div>
    );
  }
}

export default Login;



